<template>
  <v-layout column>
    <v-btn class="expand-info" fab dark small @click.stop="dialog = true" v-bind:style="{ top: mapHeight - 15 + 'px' }">
      <v-icon dark>add</v-icon>
    </v-btn>
    <chart :display-zoom="true" :height="mapHeight"></chart>
    <v-footer v-bind:style="{ height: footerHeight + 'px' }" style="border-top:1px solid gray" height="" class="">
      <v-container grid-list-md text-xs-center>
  
        <v-layout row wrap justify-center class="table-journey">
          <v-flex xs4>
            <dl>
              <dt>KNOP</dt>
              <dd>{{currentCoordinate.speed}}</dd>
            </dl>
          </v-flex>
          <v-flex xs4>
            <dl>
              <dt>KNOP (MEDEL)</dt>
              <dd>{{averageSpeed.toFixed(2)}}</dd>
            </dl>
          </v-flex>
          <v-flex xs4>
            <dl>
              <dt>Bäring</dt>
              <dd>{{bearing.toFixed(0) + '°'}}</dd>
            </dl>
          </v-flex>
  
  
          <v-flex xs4>
            <dl>
              <dt>KOMPASSRIKTNING</dt>
              <dd class="dd-small">{{compassDirection}}</dd>
            </dl>
          </v-flex>
          <v-flex xs4>
            <dl>
              <dt>LATITUD</dt>
              <dd class="dd-small">{{formatPosition(this.currentCoordinate.lat) + 'N'}}</dd>
            </dl>
          </v-flex>
          <v-flex xs4>
            <dl>
              <dt>LONGITUD</dt>
              <dd class="dd-small">{{formatPosition(this.currentCoordinate.lng) + 'E'}}</dd>
            </dl>
          </v-flex>
        </v-layout>
      </v-container>
    </v-footer>
  
    <v-dialog v-model="dialog" fullscreen transition="dialog-bottom-transition" :overlay="false" class="extra-info-dialog" scrollable>
      <v-card tile>
        <v-toolbar card dark color="primary">
          <v-btn icon @click.native="dialog = false" dark>
            <v-icon>close</v-icon>
          </v-btn>
          <v-toolbar-title>Inställningar</v-toolbar-title>
        </v-toolbar>
        <v-card-text>
          <v-btn color="primary" dark @click.stop="dialog2 = !dialog2">Open Dialog 2</v-btn>
          <v-tooltip right>
            <v-btn slot="activator">Tool Tip Activator</v-btn>
            Tool Tip
          </v-tooltip>
          <v-list three-line subheader>
            <v-subheader>User Controls</v-subheader>
            <v-list-tile avatar>
              <v-list-tile-content>
                <v-list-tile-title>Content filtering</v-list-tile-title>
                <v-list-tile-sub-title>Set the content filtering level to restrict apps that can be downloaded</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-content>
                <v-list-tile-title>Password</v-list-tile-title>
                <v-list-tile-sub-title>Require password for purchase or use password to restrict purchase</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
          <v-divider></v-divider>
          <v-list three-line subheader>
            <v-subheader>General</v-subheader>
            <v-list-tile avatar>
              <v-list-tile-action>
                <v-checkbox v-model="notifications"></v-checkbox>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>Notifications</v-list-tile-title>
                <v-list-tile-sub-title>Notify me about updates to apps or games that I downloaded</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-action>
                <v-checkbox v-model="sound"></v-checkbox>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>Sound</v-list-tile-title>
                <v-list-tile-sub-title>Auto-update apps at any time. Data charges may apply</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-list-tile avatar>
              <v-list-tile-action>
                <v-checkbox v-model="widgets"></v-checkbox>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>Auto-add widgets</v-list-tile-title>
                <v-list-tile-sub-title>Automatically add home screen widgets</v-list-tile-sub-title>
              </v-list-tile-content>
            </v-list-tile>
          </v-list>
        </v-card-text>
  
        <div style="flex: 1 1 auto;" />
      </v-card>
    </v-dialog>
  </v-layout>
</template>

<script>
  import Chart from './Chart'
  import {
    mapGetters
  } from 'vuex'
  
  export default {
    created: function() {
      const height =
        window.innerHeight ||
        document.documentElement.clientHeight ||
        document.body.clientHeight
      this.mapHeight = height * 0.8
      this.footerHeight = height * 0.2
    },
    computed: {
      ...mapGetters([
        'currentCoordinate',
        'bearing',
        'compassDirection',
        'speed',
        'averageSpeed',
      ]),
    },
    methods: {
      formatPosition(pos) {
        const val = pos
          .toFixed(5)
          .toString()
          .replace('.', '°')
        return val.substr(0, 5) + '.' + val.substr(5)
      },
    },
    data: () => ({}),
    components: {
      Chart,
    },
  }
</script>

<style scoped>
  .expand-info {
    position: absolute;
    z-index: 10000;
    left: 50%;
    left: 0;
    right: 0;
    margin: 0 auto;
    height: 30px;
    width: 30px;
  }
  
  .extra-info-dialog {
    z-index: 10001;
  }
</style>



